
.agile:focus,.agile:active,.agile *:focus,.agile *:active{outline:none
}
.agile__list{display:block;margin:0;overflow:hidden;padding:0;position:relative;width:100%
}
.agile__track:before,.agile__track:after{content:'';display:table
}
.agile__track:after{clear:both
}
.agile__slide{display:block;float:left
}
.agile__arrow{border:none;bottom:10px;margin:0;padding:0;position:absolute;transition-duration:.3s
}
.agile__arrow:hover #arrow-svg{fill:#333
}
.agile__arrow #arrow-svg{fill:#888;height:20px;transition-duration:.3s
}
.agile__arrow[disabled]{cursor:default;opacity:.4
}
.agile__arrow--prev{left:0
}
.agile__arrow--prev #arrow-svg{transform:scale(-1, 1)
}
.agile__arrow--next{right:0
}
.agile__dots{list-style:none;margin:20px 0;padding:0;text-align:center
}
.agile__dots li{display:inline-block;margin:0 10px
}
.agile__dots li.is-current button,.agile__dots li:hover button{background-color:#888
}
.agile__dots button{background-color:#eee;border:none;border-radius:50%;cursor:pointer;display:block;height:10px;font-size:0;line-height:0;margin:0;transition-duration:.3s;width:10px
}



