.clearfix:before,.clearfix:after{content:'';display:table
}
.clearfix:after{clear:both
}
.box-link{height:100%;left:0;position:absolute;top:0;width:100%;z-index:9
}
.no-scroll{height:100%;overflow:hidden;position:static;width:100%
}
.no-transition{transition:none !important;transition-duration:0s !important
